.App {
  text-align: center;
}

.Background-1 {
  background-color: purple;
  position: fixed;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}

.App-logo {
  height: 100vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.particle-canvas {
  position:fixed !important;
  width:100%;
  height:100%;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}